import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { UserResponse } from './result.model'
import { USER_API } from '../app.api'
import { DataService } from '../service/data.service'

@Component({
  selector: 'con-result',
  templateUrl: './result.component.html',
  styleUrls: ['./result.component.css']
})
export class ResultComponent implements OnInit {

  /* VARIVEIS */
  user: any
  repos: any
  totalStars: number

  userLogin: string = '';
  error: boolean = false;

  constructor(private http: HttpClient, private dataService: DataService) {
  }

  /* funo de inicializao do component */
  ngOnInit(): void {
    // recuperar o usurio da tela de search
    this.userLogin = this.dataService.getUser()

    // chamar a funo para carregar os dados do usurio
    this.searchSubmit();
  }

  /* Funo para retornar os dados do usurio e do repositrio*/
  searchSubmit() {
    // chamada da api passando o usurio para obter os dados do mesmo
    this.http.get<UserResponse>(`${USER_API}/${this.userLogin}`)
      .subscribe((data: {}) => { this.user = data; this.error = false; }, (err: HttpErrorResponse) => {
        if (err.error instanceof Error) {
          this.error = true;
          console.log("Client-side error occured.");
        } else {
          this.error = true;
          console.log("Server-side error occured.");
        }
      });

    // chamada da api passando o usurio para obter os repositrios do usurio
    this.http.get<UserResponse>(`${USER_API}/${this.userLogin}/repos`)
      .subscribe((data: {}) => { this.repos = data; this.totalStars = this.countStart(this.repos);});
  }

  /* Funo para fazer a contagem das estrelas do repositrio */
  countStart(repos: Array<any>) {
    if (repos !== undefined) {
      let stars = 0;
      repos.forEach((repo) => {
        stars += repo.stargazers_count;
      });
      return stars;
    }
  }

}
